import React from "react"
import { Link, graphql } from "gatsby"
import styled from "styled-components"
import "react-toastify/dist/ReactToastify.css"
import Cart from "../components/Cart"
import AddToCartButton from "../components/AddtoCartButton"
import { ToastContainer } from "react-toastify"

const Product = styled.div`
  margin: 1rem auto;
  max-width: 320px;
  border: 1px dotted rebeccapurple;
  padding: 15px;
`

export default function Home({ data }) {
  const products = data.products.nodes
  const categories = data.categories.nodes

  return (
    <>
      <div
        style={{
          display: "flex",
          gap: "1rem",
          justifyContent: "center",
          textAlign: "center",
        }}
      >
        {categories &&
          categories.map(category => (
            <div
              key={category.id}
              style={{ border: "1px solid #2e2e2e", padding: 16 }}
            >
              <Link
                to={`/category/${category.slug}`}
                style={{ color: "inherit" }}
              >
                <h2>{category.name}</h2>
              </Link>
              <h4>{category.count}</h4>
            </div>
          ))}
      </div>
      <Cart />
      <button
        type="button"
        onClick={() => {
          const cart = JSON.parse(window.localStorage.getItem("wooCart"))
          const line_items = cart?.line_items?.map(item => {
            return { product_id: item.product_id, quantity: item.quantity }
          })
          const total = cart?.line_items?.reduce(
            (acc, item) => acc + item.quantity * item.price,
            0
          )
          console.log(line_items)
          console.log(total)
        }}
      >
        SENDING ORDER (kilogolja azt, ami a rendeléshez kell)
      </button>
      {products.map(product => (
        <Product key={product.id}>
          <Link to={`/product/${product.slug}`}>
            <h2 style={{ position: "relative" }}>
              {product.name}
              {product.on_sale && (
                <span
                  style={{
                    fontSize: 14,
                    borderRadius: "100%",
                    background: "#e74c3c",
                    color: "#fff",
                    padding: 10,
                    position: "absolute",
                    right: 20,
                    top: 30,
                    transform: "rotate(20deg)",
                  }}
                >
                  SALE!
                </span>
              )}
            </h2>
          </Link>
          <Link to={`/category/${product.categories[0].slug}`}>
            <p>
              <strong>{product.categories[0].name}</strong>
            </p>
          </Link>
          <img src={product.images[0].src} alt="product" width="200" />
          <div
            dangerouslySetInnerHTML={{ __html: product.short_description }}
          />
          <h4>{product.price} €</h4>
          <p
            style={{ color: `${product.stock_quantity > 0 ? `green` : `red`}` }}
          >
            {product.stock_quantity} in stock (info icon)
          </p>
          <AddToCartButton product={product} />
        </Product>
      ))}
      <ToastContainer position="top-right" autoClose={5000} />
    </>
  )
}

export const query = graphql`
  query {
    products: allWcProduct {
      nodes {
        id
        slug
        name
        on_sale
        total_sales
        catalog_visibility
        description
        short_description
        price
        regular_price
        sale_price
        average_rating
        categories {
          id
          name
          slug
        }
        images {
          src
          alt
        }
        wooCommerceId
        manage_stock
        stock_quantity
        stock_status
      }
      pageInfo {
        perPage
      }
    }
    categories: allWcCategory {
      nodes {
        id
        name
        wooCommerceId
        slug
        count
        image {
          src
          name
        }
      }
    }
  }
`
