import { updateCart } from "./updateCart"

export function addToCart(product) {
  // 1. Nézzük meg, van-e már kosár a local storage-ban
  let existingCart = JSON.parse(window.localStorage.getItem("wooCart"))
  // 2. Ha van [{id: "1", name: "termék", qty: 1}, {id: "2", name: "termék-2", qty: 3}]
  // ...a, nézzük meg, ez a termék benne van-e már a kosárban, és növeljük 1-el
  // ...b, adjuk hozzá a kosárhoz és update-eljük a kosarat

  if (existingCart) {
    const updatedCart = updateCart(existingCart, product)
    window.localStorage.setItem("wooCart", JSON.stringify(updatedCart))
  } else {
    // 3. Ha nincs
    // ... adjuk hozzá a kívánt terméket egy listában, és tegyük be a localstorage-ba
    let cart = []
    cart.push(createCartItem(product))
    window.localStorage.setItem("wooCart", JSON.stringify({ line_items: cart }))
  }
}

export function createCartItem(product) {
  return {
    name: product.name,
    product_id: product.wooCommerceId,
    quantity: 1,
    price: parseInt(product.price),
    image: product?.images[0]?.src,
  }
}
