import { createCartItem } from "./addToCart"

export function updateCart(existingCart, product) {
  const updatedProducts = getUpdatedProducts(existingCart.line_items, product)
  const updatedCart = {
    line_items: updatedProducts,
  }
  return updatedCart
}

function getUpdatedProducts(existingProductsInCart, product) {
  const productExistsIndex = isProductInCart(
    existingProductsInCart,
    product.wooCommerceId
  )
  // Ha benne van a kosárban, tehát az indexe nagyon -1-nél, akkor update-lje a terméket
  if (-1 < productExistsIndex) {
    let updatedProducts = existingProductsInCart
    let updatedProduct = updatedProducts[productExistsIndex]

    updatedProduct.quantity += 1

    return updatedProducts
  } else {
    const newProduct = createCartItem(product)
    existingProductsInCart.push(newProduct)
    return existingProductsInCart
  }
}

// return index of the product if it exists
function isProductInCart(existingProductsInCart, productId) {
  const returnItemThatExits = item => {
    if (productId === item.product_id) {
      return item
    }
  }

  const newArray = existingProductsInCart.filter(returnItemThatExits)

  return existingProductsInCart.indexOf(newArray[0])
}
